import React, { useContext } from 'react';
import { Context, PriceContext } from '../../Context';
import './Item.css';

const Item = ({ item }) => {
	const [items, setItems] = useContext(Context);
	const [price, setPrice] = useContext(PriceContext);

	const itemButtonClick = (key) => {
		const newItems = [...items];
		let newPrice = price;

		for (const value of newItems)
			if (value.id === item.id) {
				value[key] = !value[key];

				if (key === 'inBasket') {
					if (value.inBasket) newPrice += value.price;
					else newPrice -= value.price;
				}
				break;
			}

		setItems(newItems);
		setPrice(newPrice);
	};

	return (
		<div className="item">
			<div className="item-content">
				<div className="item_top">
					<img src={item.imgPath} alt="img" />
					<div
						className={item.inFavourite ? 'add-favourite favourite-active' : 'add-favourite'}
						onClick={() => itemButtonClick('inFavourite')}>
						<img
							src={item.inFavourite ? '/img/heart-solid.svg' : '/img/heart-regular.svg'}
							alt="like"
							className="item_like"
						/>
					</div>
				</div>
				<div className="item_name">{item.name}</div>
				<div className="item_bottom">
					<div className="item-price">
						<label>Цена:</label>
						<div>{item.price} руб.</div>
					</div>
					<div
						className={item.inBasket ? 'item_plus plus-active' : 'item_plus'}
						onClick={() => itemButtonClick('inBasket')}>
						<img src={item.inBasket ? '/img/check.svg' : '/img/plus.svg'} alt="plus" />
					</div>
				</div>
			</div>
		</div>
	);
};

export default Item;
