import React from 'react';

const SuccessBasket = (props) => {
	return (
		<div className="overlay">
			<div className="basket empty-basket">
				<h3>Корзина</h3>
				<div className="empty-basket_content">
					<div className="empty-basket_img success_basket">
						<img src="/img/success.jpg" alt="success" />
					</div>
					<h4>Заказ оформлен!</h4>
					<div className="empty-basket_detail">
						Ваш заказ #{Math.floor(Math.random() * (100 - 1)) + 1} скоро будет передан курьерской
						доставке
					</div>
					<button className="button" onClick={() => props.clickSuccess(false)}>
						<span>
							<img src="/img/arrow.svg" alt="arrow" />
						</span>
						Вернуться назад
					</button>
				</div>
			</div>
		</div>
	);
};

export default SuccessBasket;
