import React from 'react'
import { Link } from 'react-router-dom'
import './EmptySaved.css'

const EmptySaved = props => {
    return (
        <div className="noSaved">
            <div className="noSaved_smile">
                <img src="/img/oops.png" alt="Oops" />
            </div>
            <h3>Закладок нет :(</h3>
            <p>Вы ничего не добавляли в закладки</p>
            <Link className="button" to="/">
                <span><img src="/img/arrow.svg" alt="arrow" /></span>
                Вернуться назад
            </Link>
        </div>
    )
}

export default EmptySaved