import React from 'react';
import { Link } from 'react-router-dom';
import { CSSTransition, TransitionGroup } from 'react-transition-group';
import { useContext } from 'react/cjs/react.development';
import EmptySaved from '../../containers/EmptySaved/EmptySaved';
import { Context } from '../../Context';
import Item from '../Item/Item';
import './SavedItems.css';

const SavedItems = (props) => {
	const [items] = useContext(Context);

	const savedItems = items.filter((item) => item.inFavourite);

	return (
		<div className="content">
			<h1>
				<Link to="/">
					<img src="/img/back.png" alt="back" />
				</Link>{' '}
				Мои закладки
			</h1>
			{savedItems.length ? (
				<TransitionGroup className="items">
					{savedItems.map((item, ind) => (
						<CSSTransition key={item.id} timeout={400} classNames="saved-item">
							<Item item={item} key={ind} />
						</CSSTransition>
					))}
				</TransitionGroup>
			) : (
				<EmptySaved />
			)}
		</div>
	);
};

export default SavedItems;
