import React from 'react'
import './EmptyBasket.css'

const EmptyBasket = props => {
    return (
            <div className="basket empty-basket">
                <h3>Корзина</h3>
                <div className="empty-basket_content">
                    <div className="empty-basket_img">
                        <img src="/img/box.png" alt="box" />
                    </div>
                    <h4>Корзина пустая</h4>
                    <div className="empty-basket_detail">
                        Добавьте хотя бы одну пару кроссовок, чтобы сделать заказ.
                    </div>
                    <button className="button" onClick={() => props.clickBasket(false)}>
                        <span><img src="/img/arrow.svg" alt="arrow" /></span>
                        Вернуться назад
                    </button>
                </div>
            </div>
    )
}

export default EmptyBasket