import React, { useContext } from 'react';
import { CSSTransition, TransitionGroup } from 'react-transition-group';
import BasketItem from '../../components/BasketItem/BasketItem';
import { Context, PriceContext } from '../../Context';
import EmptyBasket from '../EmptyBasket/EmptyBasket';
import './Basket.css';

const Basket = (props) => {
	const [items, setItems] = useContext(Context);
	const [, setPrice] = useContext(PriceContext);
	let totalPrice = 0;

	const basketItems = items.filter((item, ind) => {
		if (item.inBasket) {
			totalPrice += item.price;
			return true;
		}
		return false;
	});

	const orderClick = () => {
		const newItems = [...items];

		for (const item of newItems) if (item.inBasket) item.inBasket = false;

		setItems(newItems);
		setPrice(0);
		props.clickSuccess(true);
		props.clickBasket(false);
	};

	return (
		<div className="overlay">
			{basketItems.length ? (
				<div className="basket">
					<h3>Корзина</h3>
					<div className="basket_top">
						<TransitionGroup className="basket_items">
							{basketItems.map((item, ind) => (
								<CSSTransition key={item.id} timeout={400} classNames="basket-item">
									<BasketItem item={item} key={item.id} />
								</CSSTransition>
							))}
						</TransitionGroup>
					</div>
					<div className="basket_bottom">
						<div className="basket_sale">
							<label>Скидка:</label>
							<div>10%</div>
						</div>
						<div className="basket_sum">
							<label>Итого:</label>
							<div>{Math.floor(totalPrice * 0.9)} руб.</div>
						</div>
						<button className="button" onClick={orderClick}>
							Оформить заказ
						</button>
					</div>
				</div>
			) : (
				<EmptyBasket clickBasket={props.clickBasket} />
			)}
		</div>
	);
};

export default Basket;
